<template>
    <div class="app-container">
        <div class="aaaa" id="echat1"></div>
        <div class="echat-family" id="echat2"></div>
        <div class="aaaa" id="echat3"></div>

        <!-- <div class="aaaa">
        <img id="barcode" style="height:100px;width:250px"/>

        <img class="qrcodeImg" :src="qrcodeImg" style="height:250px;width:250px"/>
        </div> -->
    </div>
</template>
<script>
import JsBarcode from 'jsbarcode'
import jrQrcode from "jr-qrcode"
export default {
        data(){
            return{
            }
        },
        mounted(){
            this.getdata()

        },
        methods:{
          echatFamily(){
            let option = {
                title: {
                    text: 'Graph '
                },
                tooltip: {},
                animationDurationUpdate: 1500,
                animationEasingUpdate: 'quinticInOut',
                series: [
                    {
                        type: 'graph',
                        layout: 'none',
                        symbolSize: 30,
                        roam: true,
                        label: {
                            show: true
                        },
                        edgeSymbol: ['circle'],
                        edgeSymbolSize: [4, 10],
                        edgeLabel: {
                            fontSize: 15
                        },
                        data: [{
                            id:"11",
                            name: '1',
                            x: 500,
                            y: 300
                        }, {
                            id:"21",
                            name: '2',
                            x: 600,
                            y: 300
                        }, {
                            id:"31",
                            name: '3',
                            x: 700,
                            y: 300
                        }, {
                            id:"41",
                            name: '4',
                            x: 600,
                            y: 350
                        }],
                        links: [{
                            source: '11',
                            target: '31'
                        }, {
                            source: '31',
                            target: '21'
                        }, {
                            source: '21',
                            target: '41'
                        }],
                        lineStyle: {
                            opacity: 0.9,
                            width: 2,
                            curveness: 0
                        }
                    }
                ]
            }
            let myChart = this.$echarts.init(document.getElementById("echat2"))
            myChart.setOption(option)
            myChart.on('click', function (params) {
                console.log(params);
            });
          },
          getEchataaa(){
              let myChart = this.$echarts.init(document.getElementById("echat1"))
              myChart.setOption({
                  title: {
                      text: ''
                  },
                  tooltip: {
                      trigger: 'axis'
                  },
                  legend: {
                      data: ['', '', '', '', '']
                  },
                  grid: {
                      left: '3%',
                      right: '4%',
                      bottom: '3%',
                      containLabel: true
                  },
                  toolbox: {
                      feature: {
                          saveAsImage: {}
                      }
                  },
                  xAxis: {
                      type: 'category',
                      boundaryGap: false,
                      data: ['', '', '', '', '', '', '']
                  },
                  yAxis: {
                      type: 'value'
                  },
                  series: [
                      {
                          name: '',
                          type: 'line',
                          stack: '',
                          data: [120, 132, 101, 134, 90, 230, 210]
                      },
                      {
                          name: '',
                          type: 'line',
                          stack: '',
                          data: [220, 182, 191, 234, 290, 330, 310]
                      },
                      {
                          name: '',
                          type: 'line',
                          stack: '',
                          data: [150, 232, 201, 154, 190, 330, 410]
                      },
                      {
                          name: '',
                          type: 'line',
                          stack: '',
                          data: [320, 332, 301, 334, 390, 330, 320]
                      },
                      {
                          name: '',
                          type: 'line',
                          stack: '',
                          data: [820, 932, 901, 934, 1290, 1330, 1320]
                      }
                  ]
              })
          },
          getdata(){
            //echart
            this.echatFamily()
            //echat
            this.getEchataaa()
            //
            // this.getma();
            //
            // this.geterweima();
          },
          //
          getma(){
          this.$nextTick(()=>{
              JsBarcode("#barcode", "6921489013609", {
                  
                  width:1,//
                  height:100,//
                  displayValue:false,//
                  fontOptions:"bold italic",//
                  font:"fantasy",//
                  textAlign:"left",//
                  textPosition:"top",//
                  textMargin:5,//
                  fontSize:15,//
                  background:"#eee",//
                  lineColor:"#000000",//
                  margin:15//
                  });
              // JsBarcode("#barcode", "6921489013609");
          })

          },


          /**
          @param: text: 
          @param: options: {
          padding       : 10,   // 10px
          width         : 256,  // 256px
          height        : 256,  // 256px
          correctLevel  : QRErrorCorrectLevel.H,    // level
          reverse       : false,        // 
          background    : "#ffffff",    // 
          foreground    : "#000000"     // 
          }
          @return: Base64 URL
          */
          geterweima(){
              let options={
                  padding       : 10,   // 10px
                  width         : 256,  // 256px
                  height        : 256,  // 256px
                  correctLevel  : QRErrorCorrectLevel.H,    // level
                  reverse       : false,        // 
                  background    : "#ffffff",    // 
                  foreground    : "#000000"     // 
              }
              let text = "zzzz"
              let imgBase64 = jrQrcode.getQrBase64(text, options);  //options
              this.qrcodeImg=imgBase64
          }
            
        }
}
</script>
<style scoped>
    .aaaa{
        height: 500px;
        width: 700px;
        float: left;
    }
    .echat-family{
      height: 700px;
      width: 200px;
      float: left;
    }
</style>


