<template>
<!-- ------ -->
<div class="app-container">
  <el-scrollbar style="width:100%">
  <div class="container-table">
      <div class="container-btn" >
          <span class="container-btn-left">
            <el-button type="primary"   @click="handleAdd"></el-button>
            <el-button type="primary"   v-has="'sysRole:updateStatus'" @click="saveEnable('Y')"></el-button>
            <el-button type="danger"  :disabled="dicDisabled"  v-has="'sysRole:updateStatus'" @click="saveEnable('N')"></el-button>
            <el-button  type="danger" :disabled="dicDisabled" @click="handleDeleteBatch" v-has="'sysRole:batchDelete'" ></el-button>
          </span>
          <span class="container-btn-right">
            <el-input v-model="condition.name" placeholder="" style="width: 200px;"></el-input>
            <el-button type="primary"  icon="search"  @click="handleSearch"></el-button>
            <el-button  type="text"  @click="handlereset"></el-button>
          </span>
      </div>
      <div class="common-table-style">
      <el-table :data="pageData.list" border ref="multipleTable" align="center" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center" ></el-table-column>
            <el-table-column type="index" label="" sortable width="55" align="center" > </el-table-column>
            <el-table-column prop="name" label=""  align="center"></el-table-column>
            <el-table-column prop="remark" label="" align="center"  ></el-table-column>
            <el-table-column prop="status" label="" align="center" >
              <template slot-scope="scope">
                <div v-for="item in options" :key="item.value">
                  <el-tag
                    :type="item.label==''?'success':'danger'"
                    v-if="scope.row.status==item.value"
                  >{{item.label}}</el-tag>
                </div>
              </template>
            </el-table-column>
            <el-table-column align="center" label="" >
              <template slot-scope="scope">
                  <el-button icon="el-icon-edit" type="text" class="green"  @click="handleEdit(scope.row)"></el-button>
                  <el-button icon="el-icon-delete" v-has="'sysRole:delete'" type="text" class="red"@click="handleDelete(scope.row.id)"></el-button>
                  <el-button icon="el-icon-edit" type="text" class="green"  @click="handleRole(scope.row.id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          </div>
          <div class="pagination">
            <pagination :page-list="pageData" @pagesearch="handlePage"></pagination>
          </div>
    </div>
  </el-scrollbar>
    <div class="show-dialog">
      <el-dialog fullscreen :visible.sync="treeVisible" :modal="false" :show-close="false" :modal-append-to-body="false" @close="cancelTreeVisible">
            <div class="dialog-button">
                <el-button type="primary"   v-has="'rolePermission:save'" @click="saveCheckedNodes"> </el-button>
                <el-button  @click="cancelTreeVisible"> </el-button>
            </div>
            <el-tree
              :data="treeData"
              ref="menuTree"
              :visible.sync="treeVisible"
              highlight-current
              show-checkbox
              :props="defaultProps"
              node-key="id"
            ></el-tree>
      </el-dialog>
    </div>
    <!--  end -->

    <!--  -->
    <div class="show-dialog" v-if="editVisible">
      <el-dialog
        fullscreen
        :show-close="false"
        :close-on-click-modal="false"
        :visible.sync="editVisible"
        width="45%"
        :modal="false"
        :modal-append-to-body="false"
      >
        <div class="dialog-button">
          <el-button type="primary"   v-has="'sysRole:save'" @click="saveEdit('form')"> </el-button>
          <el-button  @click="editVisible = false"> </el-button>
        </div>
        <el-form class="padT" ref="form"  :model="form" :rules="rules" label-width="80px">
          <el-form-item label="" prop="name" style="width:25rem">
            <el-input v-model="form.name" ></el-input>
          </el-form-item>
          <el-form-item label="" >
            <el-select v-model="form.status" placeholder="" style="width:25rem">
              <el-option
                clearable
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-col :span="24">
              <el-input
                maxlength="200"
                :autosize="{ minRows: 6, maxRows: 10}"
                :rows="8"
                type="textarea"
                style="max-width: 35rem;"
                @input="descInput_fou()"
                v-model="form.remark"
              ></el-input>
            </el-col>
            <el-col>
              <span>{{remnant_fou}}/200</span>
            </el-col>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>

</div>
  
</template>
<script>
export default {
  name:"sysRole",
  data(){
    return{
      show:true,
      pageData: {
        list: [],
        pageNumber: 1,
        pageSize: 10,
        totalCount: 0,
        totalPage: 0
      },
      //
      condition: {
        name: "",
        status: null
      },
      options:[],//
      dicDisabled:true,
      multipleSelection:[],//
      //
      form: {
        id: "",
        name: "",
        status: "",
        description: ""
      },
      editVisible: false,
      remnant_fou: 200,
      btnName: true,
      btnDes: true,
      btnStatus: true,
      rules: {
        name: [
          {
            required: true,message: "  ",trigger: "blur"
          }
        ]
      },
      treeData:[],
      treeVisible: false,
      defaultProps: {
        children: "children",
        label: "menuName"
      },
      currentId:"",
    }
  },
  created(){
    this.getData();
  },

  methods:{
    //
    getData() {
      
      this.$http.post(this.api.roleQuerybycondition, {
          condition: this.condition,
          number: this.pageData.pageNumber,
          size: this.pageData.pageSize
        })
        .then(res => {
          if (res.data.code === "200") {
            this.pageData.list = res.data.body.records;
            this.pageData.totalCount = res.data.body.total;
            this.pageData.totalPage = res.data.body.pages;
          }
        });
        this.getType("enabled");
    },
    //
    handlePage(number, size) {
      this.pageData.pageNumber=number;
      this.pageData.pageSize=size;
      this.getData();
    },
    //
    handleSearch(){
      this.getData();
    },
    //
    handlereset(){
      condition= {
        name: "",
        status: null
      }
      this.getData();
    },
    //
    handleEdit(row){
      // 
      this.form = Object.assign({}, row);
      this.editVisible = true;
    },
    //
    handleDelete(id){
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
          this.$http.delete(this.api.deleteById + this.id).then(res => {
            if (res.data && res.data.code == "200") {
              this.$message({
                message: "",
                type: "success"
              });
            } else {
              this.$message.warning(`${(res.data && res.data.body)||''}`);
            }
            this.handleSearch();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });

    },
    //---start
    handleRole(id){
      //
      this.$http.get(this.api.roleGetButMenu).then(res => {
        if (res.data.code == "200") {
          this.treeData = res.data.body;
          this.treeVisible = true;
          this.currentId = id;
        }
        //
        this.$http.get(this.api.roleGetButByRoleId + id).then(res => {
          if (res.data.code == "200") {
            this.$refs.menuTree.setCheckedKeys(res.data.body);
          }
        });
      });
    },
    //
    cancelTreeVisible() {
      this.treeVisible = false;
    },
    //
    saveCheckedNodes() {
      let data = this.$refs.menuTree.getCheckedNodes(false, true);
          let saveData = [];
          data.forEach(i => {
            if (i.sysType == "menu") {
              saveData.push({
                roleId: this.currentId,
                menuId: i.id,
                parentId: i.parentId
              });
            } else {
              saveData.push({
                roleId: this.currentId,
                buttonId: i.id,
                menuId: i.parentId
              });
            }
          });
          this.$http
            .put(this.api.rolePermissionBatchsave + this.currentId, saveData)
            .then(res => {
              if (res.data.code == "200") {
                this.$message.success("");
                this.treeVisible = false;
              }
            });
    },
    //---end
    //
    handleSelectionChange(val){
      if (val.length > 0) {
        this.multipleSelection = val;
        this.dicDisabled = false;
      } else {
        this.dicDisabled = true;
      }
    },
    //
    handleAdd(){
      this.form = {
        roleName: "",
        roleCode: "",
        status: "1",
        description: "",
        id: ""
      };
      this.editVisible = true;
    },
    //
    saveEnable(type){
      let changeData;
      if (type === "Y") {
        changeData = this.multipleSelection.filter(i => {
          if (i.status === "0") {
            i.status = "1";
            return i;
          }
        });
      } else {
        changeData = this.multipleSelection.filter(i => {
          if (i.status === "1") {
            i.status = "0";
            return i;
          }
        });
      }
      this.$http.put(this.api.roleBatchUpdate, changeData).then(res => {
        this.getData();
      });

    },
    // 
    saveEdit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$http.post(this.api.roleSaveorupdate, this.form).then(res => {
            if (res.data.code === "200") {
              this.$message.success(res.data.body);
              this.getData();
              this.editVisible = false;
            } else {
              this.$message.error(res.data.body);
            }
          });
        } else {
          return false;
        }
      });
    },
    //
    descInput_fou() {
      var txtVal = this.form.remark.length;
      this.remnant_fou = 200 - txtVal;
    },
    //
    handleDeleteBatch(){

      const deldata = [];
      this.multipleSelection.filter(i => {
        deldata.push(i.id);
      });
      this.$http.post(this.api.roleBatchDelete, deldata).then(res => {
        if (res.body && res.body.code === '200') {
          this.$message({
            message: "",
            type: "success"
          });
        } else {
          this.$message.warning(`${(res.data && res.data.body)||''}`);
        }
        this.getData();
      });

    },
    //
    getType(type) {
      this.$http.get(this.api.dicTypeGetType + type).then(res => {
        if (res.data.code == 200) {
          this.options = res.data.body;
        }
      });
    },

  }
  
}
</script>
<style lang="scss" scoped>

</style>


