                <el-button  @click="backHandle"></el-button>
                <el-button type="primary" @click="saveHandle"></el-button>
            </div>
            <div class="dialog-form">
                <el-form ref="form" label-width="150px" :rules="rules" :model="form">
                    <el-form-item label="" prop="testName">
                        <el-input v-model="form.testName"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="createTime">
                        <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="form.createTime" type="datetime" placeholder=""></el-date-picker>
                    </el-form-item>

                    <el-form-item label="" prop="testSelect">
                        <el-input v-model="form.testSelect"></el-input>
                    </el-form-item>
                </el-form>
            </div>

    <!-- <div class="dialog-tab">
          <el-tabs v-model="activeName">
            <el-tab-pane label="" name="first">
              <div class="container-btn">
                <el-button icon="el-icon-plus"  type="primary" @click="addList"></el-button>
                <el-button icon="el-icon-minus"  type="danger" @click="delList"></el-button>
              </div>
              <div class="tab-table">
                <el-table @selection-change="handleSelectionChangeDetil" ref="tableData" :data="form.testSysTetailList"
                          border align="center">

                  <el-table-column type="selection" fixed width="45" align="center"
                                   show-overflow-tooltip></el-table-column>
                  <el-table-column type="index" width="55" label="" align="center"
                                   show-overflow-tooltip></el-table-column>

                  <el-table-column prop="name" min-width="200" label="" show-overflow-tooltip>
                    <template slot-scope="scope">
                      <div>
                        <el-input  v-model="scope.row.name"></el-input>
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column prop="status" min-width="200" label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                      <div>
                        <el-select v-model="scope.row.status"  placeholder="" style="width:100%;">
                          <el-option
                            v-for="item in statusList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                          ></el-option>
                        </el-select>

                      </div>
                    </template>
                  </el-table-column>

                  <el-table-column prop="money" min-width="200" label="" align="center">
                    <template slot-scope="scope">
                      <currencyInput v-model="scope.row.money"></currencyInput>
                    </template>
                  </el-table-column>


                  <el-table-column min-width="200" label="" align="center" show-overflow-tooltip>
                    
                  </el-table-column>
                </el-table>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div> -->

        </el-dialog>
    </div>
</template>
<script>
export default {
    name: "oneEdit",
    data() {
      return {
        editVisible: false,
        //
        form: {},
        //
        selectData: {
        },
        //
        rules:{
        }
        
      }
    },
    methods:{
        //
        clearCheck(){
            this.$refs['form'].resetFields()
        },
        //
        empty(){
            this.form = {
                id:"",
                testName:"",
                testType:"",
                testStatus:"",
                createTime:"",
                createUser:"",
                parentId:"",
                testArea:"",
                testNum:"",
                testUnit:"",
            }
        },
        //
        openByNew(){
            this.editVisible = true
            this.empty()
        },
        //
        openByEdit(row){
            this.openByNew()
            this.$http.get(this.api.sysTestqueryById + row.id).then(res => {
                this.form = res.data.body
            });
        },
        //
        backHandle(){
            this.clearCheck()
            this.editVisible = false
        },
        //
        saveHandle(){
            this.$refs['form'].validate((valid)=>{
              if(valid){
                this.$confirm("?", "", {
                  confirmButtonText: "",
                  cancelButtonText: "",
                  type: "warning"
                }).then(() => {
                    this.$http.post(this.api.sysTestsaveOrUpdate,this.form).then(res => {
                      if(res.data.code == '200'){
                        this.$message.success(res.data.message)
                        this.editVisible = false
                        this.getData()
                      }
                    });
                })
              }
            })
        }
    }
    
}
</script>


