<template>
    <div class="app-container">
        <!--  -->

        <el-scrollbar style="width:100%">
        <template v-show="frameFontvisible">
        <div class="container-query">
            <queryItem label="">
                <el-input v-model="queryContion.temName" placeholder=""></el-input>
            </queryItem>
            <query-item isButton style="width:100%;padding-left:30px">
                <el-button type="primary"  @click="search" ></el-button>
                <el-button  plain @click="reset" ></el-button>
                <el-button  plain @click="handleAdd" ></el-button>
            </query-item>
        </div>
        <!--  -->
        <div class="container-table">
            <div class="common-table-style">
                <el-table :data="pageData.list" border
                >
                    <el-table-column type="index" width="50" label=""></el-table-column>
                    <el-table-column prop="temPlateName"  label=""></el-table-column>
                    <el-table-column prop="createTime"  label=""></el-table-column>
                    <el-table-column prop="createUser"  label=""></el-table-column>
                    <el-table-column  label="">
                      <template slot-scope="scope">
                            <el-button @click="handleEdit(scope.row)" type="text"></el-button>
                            <el-button @click="handleDelete(scope.row)" type="text" style="color:red"></el-button>
                      </template>
                    </el-table-column>
                  </el-table>
            </div>
            <div class="pagination">
              <pagination :page-list="pageData" @pagesearch="handlePage"></pagination>
            </div>
        </div>
        </template>
        </el-scrollbar>
        <template v-if="!frameFontvisible">
            <editLayout ref="editLayout" isFromTem @backfont="backfont"></editLayout>
        </template>
    </div>
</template>
<script>
import editLayout from "../codeAuto/editLayout"
export default {
    components: { editLayout },
    name: "sysAutoTemQuery",
    data() {
      return {
        // 
        pageData: {
          list: [],
          pageNumber: 1,
          pageSize: 10,
          totalCount: 0,
          totalPage: 0
        },
        // 
        queryContion: {
          temName:"",
        },
        //
        selectData: {
        },
        frameFontvisible:true
      }
    },
    created() {
      //
      this.getData()
      //
      this.getDropData()
    },
    methods:{
        //
        getDropData(){
        },
        // 
        empty(){
            this.queryContion = {
                temName:"",
            }
        },
        //
        getData(){
          this.queryContion.isTemplate = '1'
            let queryContion = {
                condition: this.queryContion,
                number: this.pageData.pageNumber,
                size: this.pageData.pageSize
            }
            this.$http.post(this.api.sysAutoTableQuerybycondition,queryContion)
            .then(res => {
              this.pageData.list = res.data.body.records;
              this.pageData.totalCount = res.data.body.total;
              this.pageData.totalPage = res.data.body.pages;
            })
        },

        // 
        search(){
            this.getData()
        },
        // 
        reset(){
            this.empty()
            this.getData()
        },
        //
        handlePage(number, size){
            this.pageData.pageNumber = number
            this.pageData.pageSize = size
            this.getData()
        },

        //
        handleEdit(row) {
          
          this.$refs.editLayout.openByEdit(row)
        },
        //
        handleDelete(row){
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }).then(() =>
          {
              this.$http.delete(this.api.sysAutoTabledeleteById + row.id)
              .then(res => {
                if(res.data.code == '200'){
                  this.$message.success(res.data.message)
                  this.getData()
                }
              })
          })
        },

        //
        handleAdd() {
          this.frameFontvisible = false
        },
        backfont(){
          this.frameFontvisible = true
          this.getData()
        }
    }
}
</script>


