<template>

    <div class="app-container">
        <!--  -->
        <div class="container-query">
            <queryItem label="">
                <el-input v-model="queryContion.vvvv" placeholder=""></el-input>
            </queryItem>
            <queryItem  label="" style="width:50%">
                <div class="is_data">
                  <el-date-picker
                    v-model="queryContion.createTimeFrom"
                    type="date"
                    placeholder="">
                  </el-date-picker>
                  <span style="margin: 0 10px;line-height: 36px;">  </span>
                  <el-date-picker
                    v-model="queryContion.createTimeTo"
                    type="date"
                    placeholder="">
                  </el-date-picker>
                </div>
            </queryItem>
            <query-item isButton style="width:100%;padding-left:30px">
                <el-button type="primary"  @click="search" ></el-button>
                <el-button  plain @click="reset" ></el-button>
                <el-button  plain @click="handleAdd" ></el-button>
                <el-button  plain @click="handleDeleteBatch" ></el-button>
            </query-item>
        </div>
        <!--  -->
        <div class="container-table">
            <div class="common-table-style">
                <el-table :data="pageData.list" border
                 @selection-change="handleSelectionChange"
                >
                    <el-table-column type="selection"></el-table-column>
                    <el-table-column type="index" width="50" label=""></el-table-column>
                    <el-table-column prop="vvvv"  label=""></el-table-column>
                    <el-table-column prop="createTime"  label=""></el-table-column>
                    <el-table-column prop="createUser"  label=""></el-table-column>
                    <el-table-column  label="">
                      <template slot-scope="scope">
                            <el-button @click="handleEdit(scope.row)" type="text"></el-button>
                            <el-button @click="handleDelete(scope.row)" type="text" style="color:red"></el-button>
                            <el-button type="text" ></el-button>
                      </template>
                    </el-table-column>
                  </el-table>
            </div>
            <div class="pagination">
              <pagination :page-list="pageData" @pagesearch="handlePage"></pagination>
            </div>
        </div>
        <vvvvEdit ref="vvvvEdit" @back="getData"></vvvvEdit>
    </div>
</template>
<script>
import vvvvEdit from "./edit"
export default {
    components: { vvvvEdit },
    name: "vvvvQuery",
    data() {
      return {
        // 
        pageData: {
          list: [],
          pageNumber: 1,
          pageSize: 10,
          totalCount: 0,
          totalPage: 0
        },
        // 
        queryContion: {
          vvvv:"",
          createTime:"",
          createUser:"",
        },
        //
        selectData: {

        },
        //
        delVal:""
      }
    },
    created() {
      //
      this.getData()
      //
      this.getDropData()
    },
    methods:{
        //
        getDropData(){

        },
        // 
        empty(){
            this.queryContion = {
                vvvv:"",
                createTime:"",
                createUser:"",
            }
        },
        //
        getData(row){
            let queryContion = {
                condition: this.queryContion,
                number: this.pageData.pageNumber,
                size: this.pageData.pageSize
            }
            this.$http.post("api/system/vvvv/query",queryContion)
            .then(res => {
              this.pageData.list = res.data.body.records;
              this.pageData.totalCount = res.data.body.total;
              this.pageData.totalPage = res.data.body.pages;
            })
        },

        // 
        search(){
            this.getData()
        },
        // 
        reset(){
            this.empty()
            this.getData()
        },
        //
        handlePage(number, size){
            this.pageData.pageNumber = number
            this.pageData.pageSize = size
            this.getData()
        },

        //
        handleEdit(row) {
          this.$refs.vvvvEdit.openByEdit(row)
        },
        //
        handleDelete(row){
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }).then(() =>
          {
              this.$http.delete("api/system/vvvv/deleteById/" + row.id)
              .then(res => {
                if(res.data.code == '200'){
                  this.$message.success(res.data.message)
                  this.getData()
                }
              })
          })
        },

        //
        handleAdd() {
          this.$refs.vvvvEdit.openByNew()
        },
        handleDeleteBatch(){
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
          .then(() => {
            let deletebatch = this.$utils.findIds(this.delVal)
            this.$http.post("api/system/vvvv/batchDelete",deletebatch)
            .then(res => {
              if(res.data.code == '200'){
                this.$message.success(res.data.message)
                this.getData()
              }
            })
          })
        },
        //
        handleSelectionChange(val){
          this.delVal = val
        }
    }
}
</script>


