<template>

    <div class="app-container">
        <!--  -->
        <el-scrollbar style="width:100%">
        <div class="container-query">
            <queryItem label="">
                <el-input v-model="queryContion.terminalModel" placeholder=""></el-input>
            </queryItem>
            <queryItem label="">
                <el-input v-model="queryContion.terminalName" placeholder=""></el-input>
            </queryItem>
            <query-item isButton style="width:100%;padding-left:30px">
                <el-button type="primary"  @click="search" ></el-button>
                <el-button  plain @click="reset" ></el-button>
                <el-button  plain @click="handleAdd" ></el-button>
            </query-item>
        </div>
        <!--  -->
        <div class="container-table">
            <div class="common-table-style">
                <el-table :data="pageData.list" border
                >
                    <el-table-column type="index" width="50" label=""></el-table-column>
                    <el-table-column prop="terminalModel"  label=""></el-table-column>
                    <el-table-column prop="terminalName"  label=""></el-table-column>
                    <el-table-column prop="terminalNumber"  label=""></el-table-column>
                    <el-table-column prop="terminalNameType"  label=""></el-table-column>
                    <el-table-column  label="">
                      <template slot-scope="scope">
                            <el-button @click="handleEdit(scope.row)" type="text"></el-button>
                            <el-button @click="handleDelete(scope.row)" type="text" style="color:red"></el-button>
                      </template>
                    </el-table-column>
                  </el-table>
            </div>
            <div class="pagination">
              <pagination :page-list="pageData" @pagesearch="handlePage"></pagination>
            </div>
        </div>
        
        </el-scrollbar>
        <phoneModelEdit ref="phoneModelEdit" @back="getData"></phoneModelEdit>
    </div>
</template>
<script>
import phoneModelEdit from "./edit"
export default {
    components: { phoneModelEdit },
    name: "phoneModelQuery",
    data() {
      return {
        // 
        pageData: {
          list: [],
          pageNumber: 1,
          pageSize: 10,
          totalCount: 0,
          totalPage: 0
        },
        // 
        queryContion: {
          terminalModel:"",
          terminalName:"",
        },
        //
        selectData: {

        },
      }
    },
    created() {
      //
      this.getData()
      //
      this.getDropData()
    },
    methods:{

        //
        handlePage(number, size){
            this.pageData.pageNumber = number
            this.pageData.pageSize = size
            this.getData()
        },
        //
        getDropData(){

        },
        // 
        empty(){
            this.queryContion = {
                terminalModel:"",
                terminalName:"",
            }
        },
        //
        getData(row){
            let queryContion = {
                condition: this.queryContion,
                number: this.pageData.pageNumber,
                size: this.pageData.pageSize
            }
            this.$http.post("api/system/phoneModel/query",queryContion)
            .then(res => {
              this.pageData.list = res.data.body.records;
              this.pageData.totalCount = res.data.body.total;
              this.pageData.totalPage = res.data.body.pages;
            })
        },

        // 
        search(){
            this.getData()
        },
        // 
        reset(){
            this.empty()
            this.getData()
        },

        //
        handleEdit(row) {
          this.$refs.phoneModelEdit.openByEdit(row)
        },
        //
        handleDelete(row){
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }).then(() =>
          {
              this.$http.delete("api/system/phoneModel/deleteById/" + row.id)
              .then(res => {
                if(res.data.code == '200'){
                  this.$message.success(res.data.message)
                  this.getData()
                }
              })
          })
        },

        //
        handleAdd() {
          this.$refs.phoneModelEdit.openByNew()
        },
    }
}
</script>


