<template>
    <div class="app-container" style="flex-direction:column;">
        <div class="container">
            <div class="container_btn">
                <el-button type="primary" size="small" buttonCode="TY020" @click="handleSave('form')"> </el-button>
                <el-button size="small" @click="cancelSave"> </el-button>
            </div>

            <el-tabs v-model="activeName" @tab-click="handleClick">
              <el-tab-pane label="" name="first">
            <el-form  ref="form" :model="form" :rules="rules" label-width="150px" size="small" :inline="false">
              <el-col :xs="24" :sm="24" :md="12" :lg="6" :xl="6" :offset="2">
                  <el-form-item  prop="headPortrait" style="margin-left:-35px">
                    <img  :src="form.headPortrait" style="height:120px;width:120px;border-radius:50%;"/>
                  </el-form-item>
                  <el-button @click="changeUserImgDialog=true" style="margin-left:120px;" buttonCode="GR001"></el-button>
                <!-- </div> -->
              </el-col>
              <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8">
                <el-form-item label="" prop="userName">
                  <el-input v-model="form.userName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="loginUser">
                  <el-input v-model="form.loginUser"></el-input>
                </el-form-item>
                <el-form-item label="" prop="gender">
                  <el-select v-model="form.gender" clearable placeholder="" style="width: 100%" >
                    <el-option
                      v-for="item in genderData"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="qq/" prop="qqWeixin">
                  <el-input v-model="form.qqWeixin"></el-input>
                </el-form-item>
                <el-form-item label="" prop="email">
                    <el-input v-model="form.email"></el-input>
                </el-form-item>
              </el-col>
            </el-form>

              </el-tab-pane>
              <el-tab-pane label="" name="second">
                 <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8"  style="height:50px">
                 </el-col>
                  <el-form :model="formPass" ref="formPass" :rules="rules" label-width="100px" :inline="true" size="small">
                    <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" >
                      <el-form-item label="" prop="password" class="password_input">
                        <el-input v-model="formPass.password" :type="pwdType1" ></el-input>
                        <span :class="passIcon1" @click="changeType1()" style="position: absolute;top:10px;right: 8px;"></span>
                      </el-form-item>
                      <br>
                      <el-form-item label="" prop="newPassword" class="password_input">
                        <el-input v-model="formPass.newPassword" :type="pwdType2"></el-input>
                        <span :class="passIcon2" @click="changeType2()"  style="position: absolute;top:10px;right: 8px;"></span>
                      </el-form-item>
                      <br>
                      <el-form-item label="" prop="confirmPassword" class="password_input">
                        <el-input v-model="formPass.confirmPassword" :type="pwdType3"></el-input>
                        <span :class="passIcon3" @click="changeType3()"  style="position: absolute;top:10px;right: 8px;"></span>
                      </el-form-item>
                    </el-col>
                  </el-form>
              </el-tab-pane>
            </el-tabs>
        </div>
     

            <!--  -->
            <el-dialog
              modal-append-to-body
              title=""
              :visible.sync="changeUserImgDialog"
              :before-close="handleClose"
              width="30%"
            >
              <!--  -->
              <upload v-on:listenTitleImg = "showTitleImg" :avatarSrc="getAvatar"></upload>
              <el-button @click="handleClose" class="cancelBtn"> </el-button>
            </el-dialog>
    </div>

</template>

<script>
import { mapGetters } from 'vuex';
import bus from "@utils/common/bus";
export default {
  name: "userInfo",
  data(){
    // 
		var checkPhone = (rule, value, callback) => {
      const phoneReg = /^1[3|4|5|6|7|8][0-9]{9}$/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        
        if (!Number.isInteger(+value)) {
          callback(new Error(''))
        } else {
          if (phoneReg.test(value)) {
            callback()
          } else {
            callback(new Error(''))
          }
        }
      }, 100)
    };
    // 
    var checkEmail = (rule, value, callback) => {
      const mailReg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }, 100)
    };
    // 
    var checkNewPassword = (rule,value,callback)=> {
      const newPasswordReg = /^[0-9A-Za-z]{6,}$/
      setTimeout(() => {
        if (newPasswordReg.test(value)) {
          callback()
        } else {
          this.$message.warning(`(6) `);
          callback(new Error('(6) '))
        }
      }, 100)
    };
    return{
      form:{
        id: "",
        userName: "",
        loginUser:"",
        mobilePhone: "",
        qqWeixin:"",
        email: "",
        gender: "",
        headPortrait: "", // 
        headPortraitSrc:"" //base64
      },
      rules:{
          userName: [
            { required: true, message: ' ', trigger: 'blur' }
          ],
          loginUser: [
            { required: true, message: ' ', trigger: 'blur' }
          ],
          mobilePhone: [
            { required: true, message: '', trigger: 'change' },
            { validator: checkPhone, trigger: 'change' }
          ],
          email: [
            { required: true, message: '', trigger: 'change' },
            { validator: checkEmail, trigger: 'change' }
          ],
          gender: [
            { required: true, message: ' ', trigger: 'change' }
          ],
          password: [
            { required: true, message: ' ', trigger: 'blur' }
          ],
          newPassword: [
            { required: true, message: ' ', trigger: 'blur' },
            { required: true, validator: checkNewPassword, trigger: 'blur' }
          ],
          confirmPassword: [
            { required: true, message: ' ', trigger: 'blur' }
          ]
      },
      activeName:"first",//tab
      genderData:[],//
      formPass: {
        password: "",
        newPassword: "",
        confirmPassword: ""
      },
      passIcon1: 'icon-mimabukejian', // 
      passIcon2: 'icon-mimabukejian', // 
      passIcon3: 'icon-mimabukejian', // 
      pwdType1: 'password', // 
      pwdType2: 'password', // 
      pwdType3: 'password', // 
      changeUserImgDialog: false, // 
    }
  },
  computed: {
    ...mapGetters([
      'getAvatar'
    ])
  },

  created(){
    //
    this.showImg()
    this.getData();
    this.getxiala();
  },
  methods:{
    //
    handleSave(){
      //tab
      var activeName = this.activeName;
      if (activeName == "first") {
        this.handleSaveInfo(this.form);
      } else {
        this.handleSavePass(this.formPass);
      }

    },

    //
    handleSaveInfo(form) {
      this.form.headPortraitSrc = this.form.headPortrait;
      this.form.headPortrait="";
      this.$refs["form"].validate(valid => {
        if (valid) {
          this.$http
            .post(this.api.userUpdateUserInfo, this.form)
            .then(res => {
              if (res.data.code == "200") {
                this.$message.success(res.data.message);
                //
                // this.$store.dispatch('changeUrlFun',res.data.body);
                sessionStorage.setItem("user", JSON.stringify(res.data.body));
                //bus
                bus.$emit("changeHead","change");

                this.showImg();
              } else {
                this.$message.warning(res.data.message);
                return false;
              }
            });
        } else {
          return false;
        }
        this.activeName = "first";
      });
    },


    //
    handleSavePass(formPass) {
      //2
      if(this.formPass.newPassword != this.formPass.confirmPassword){
          this.$message.error("");
          formPass.newPassword = "";
          formPass.confirmPassword = "";
      }
      //id
      var id = this.$User.getId()
      var name = this.$User.getLoginUser();
      var parameter = {
        condition: {
          id: id,
          password: this.formPass.password,
          newPassword: this.formPass.newPassword
        }
      };
      this.$refs["formPass"].validate(valid => {
        if (valid) {
          this.$http
            .post(this.api.userUpdatePasswordById, parameter)
            .then(res => {
              if (res.data.pages == "200") {
                this.$message.success(res.data.message);
              } else {
                this.$message.warning(res.data.message);
                return false;
              }
            });
        } else {
          return false;
        }
        this.activeName = "second";
      });
    },

    //
    cancelSave(){
      this.empty();
      this.$router.push("/page/Dashboard");
    },
    //
    empty(){
      this.form = {
        id: "",
        userName: "",
        titleId: "",
        mobilePhone: "",
        email: "",
        gender: "",
      };
      this.formPass = {
        password: "",
        newPassword: "",
        confirmPassword: ""
      };
      //
      this.$nextTick(()=>{
          this.$refs["form"].clearValidate();
          this.$refs["formPass"].clearValidate();
        });
      },
    getData(){
      //id
      var id = this.$User.getId();
      this.$http.post(this.api.userQueryById + id).then(res => {
        if (res.data.code == "200") {
          this.form = res.data.body;
        } else {
          this.$message.warning(res.data.body);
          return false;
        }
      });
    },
    //
    showImg() {
        let user = this.$User.getUser(); 
        var avatar=user.headPortrait;
        this.form.headPortrait =avatar
      },
    //
    getxiala(){
        this.$http.get(this.api.dicTypeGetType + "gender").then(res => {
          if (res.data.code == 200) {
            this.genderData = res.data.body;
          }
        });
    },
    // 
    changeType1() {
      this.passIcon1 = this.passIcon1 === 'icon-mimabukejian'? 'icon-mimabukejian_1' : 'icon-mimabukejian';
      this.pwdType1 = this.pwdType1 === 'password'? 'text' : 'password';
    },
    changeType2() {
      this.passIcon2 = this.passIcon2 === 'icon-mimabukejian'? 'icon-mimabukejian_1' : 'icon-mimabukejian';
      this.pwdType2 = this.pwdType2 === 'password'? 'text' : 'password';
    },
    changeType3() {
      this.passIcon3 = this.passIcon3 === 'icon-mimabukejian'? 'icon-mimabukejian_1' : 'icon-mimabukejian';
      this.pwdType3 = this.pwdType3 === 'password'? 'text' : 'password';
    },
    //tab
    handleClick(){
      if(this.activeName=="first"){
        this.activeName=="second"
      }
    },
    // 
    handleClose(){
      this.changeUserImgDialog=false;
    },
    // 
    showTitleImg(data) {
      this.form.headPortrait = data;
      this.changeUserImgDialog = false;
    },

  },
  
}
</script>
<style lang="scss" scoped>

</style>


