<template>
    <div class="app-container">
      <el-scrollbar style="width:100%">
        <div class="container-table">
          <!--   -->
          <div class="container-btn" >
            <span class="container-btn-left">
              <el-button type="primary" size="small" @click="handleAdd"></el-button>
              <el-button type="danger" size="small"   v-has="'sysUser:batchDelete'" @click="handleDeleteBatch"></el-button>
            </span>
            <span class="container-btn-right">
              <el-input v-model="queryContent" placeholder="" style="width: 200px;" clearable></el-input>
              <el-button type="primary" size="small" icon="search" @click="search"></el-button>
              <el-button size="small" type="text"  @click="reset"></el-button>
            </span>
          </div>
          <!-- ---+ -->
          <div class="common-table-style">
          <el-table :data="pageData.list" border ref="multipleTable" align="center" @selection-change="handleSelectionChange" >
            <el-table-column type="selection"   width="55"  align="center" show-overflow-tooltip></el-table-column>
            <el-table-column type="index" width="55" label="" align="center" show-overflow-tooltip></el-table-column>
            <el-table-column prop="userName"  label="" align="center" v-if="indexUserName" show-overflow-tooltip></el-table-column>
            <el-table-column prop="loginUser"  label="" align="center" v-if="indexloginUser" show-overflow-tooltip></el-table-column>
            <el-table-column prop="status" label="" align="center" v-if="IndexStatus" show-overflow-tooltip>
              <template slot-scope="scope">
                <div v-for="item in statusData" :key="item.value">
                  <el-tag
                    :type="item.label==''?'success':'danger'"
                    v-if="scope.row.status==item.value"
                  >{{item.label}}</el-tag>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="roleIds" label="" align="center" show-overflow-tooltip>
              <template slot-scope="scope">
                <div v-for="item in titleData" :key="item.id" >
                  <el-tag type="success" v-for="roleId in scope.row.roleIds" :key="roleId" v-if="roleId == item.id">{{item.name}}</el-tag>
                </div>
              </template>
            </el-table-column>

            <el-table-column label=""  align="center">
              <template slot-scope="scope">
                <el-button type="text" icon="el-icon-edit" @click="handleEdit(scope.row)"></el-button>
                <el-button type="text" icon="el-icon-delete"  v-has="'sysUser:deleteID'" style="color:red"  @click="handleDelete(scope.row.id)" ></el-button>
                <el-button type="text" icon="el-icon-unlock" v-has="'sysUser:resetPassword'" @click="resetPassword(scope.row)"></el-button>
              </template>
             </el-table-column>
          </el-table>
          </div>
          <div class="pagination">
            <pagination :page-list="pageData" @pagesearch="handlePage"></pagination>
          </div>
        </div>
      </el-scrollbar>
          <!-- --- -->
        <div class="show-dialog">
            <el-dialog :visible.sync="editVisible"  :show-close="false" :modal="false" fullscreen>
              <div class="dialog-button">
                  <el-button type="primary" size="small"  v-has="'sysUser:save'" @click="handleSave('form')"> </el-button>
                  <el-button size="small" @click="cancelSave"> </el-button>
              </div>
              <el-form  ref="AddForm" :model="AddForm" :rules="rules" label-width="150px" size="small" style="margin-top:10px">

                <div class="edit-area">
                  <el-form-item label="" prop="loginUser">
                    <el-input v-model="AddForm.loginUser"></el-input>
                  </el-form-item>

                  <el-form-item label="" prop="userName">
                    <el-input v-model="AddForm.userName"></el-input>
                  </el-form-item>
                
                  <el-form-item label="" prop="gender">
                    <el-select v-model="AddForm.gender" clearable placeholder="" style="width: 100%">
                      <el-option
                        v-for="item in genderData"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>


                  <el-form-item label="" prop="roleIds" >
                          <el-select v-model="AddForm.roleIds" multiple placeholder="" style="width: 100%">
                            <el-option
                                v-for="item in titleData"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id"/>
                          </el-select>
                  </el-form-item>

                  <el-form-item label="" prop="birthday">
                    <div class="block">
                      <el-date-picker
                        style="width:100%;"
                        v-model="AddForm.birthday"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="yyyy-MM-dd"
                      ></el-date-picker>
                    </div>
                  </el-form-item>

                  <el-form-item label="">
                    <el-select v-model="AddForm.status" placeholder="" style="width: 100%">
                      <el-option
                        clearable
                        v-for="item in statusData"
                        :key="item.label"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                
                  <el-form-item label="" prop="description">
                      <el-input
                        maxlength="200"
                        :autosize="{ minRows: 6, maxRows: 10}"
                        :rows="8"
                        type="textarea"
                        @input="descInput_fou()"
                        v-model="AddForm.description"
                      ></el-input>
                    <span>{{remnant_fou}}/200</span>
                  </el-form-item>
                </div>

              </el-form>
            </el-dialog>
        </div>


    </div>
</template>
<script>
export default {
  name:"sysUserList",
  data(){
    // 
    var validateMobilePhone = (rule, value, callback) => {
      if (!value) {
              return callback(new Error(''));
            } else {
              const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
              if (reg.test(value)) {
                callback();
              } else {
                return callback(new Error(''));
              }
            }
        };
    return{

      queryContent:"",//
      indexUserName:true,
      indexloginUser:true,
      IndexStatus:true,
      // --end
      //----start
        pageData: {
          list: [],
          pageNumber: 1,
          pageSize: 10,
          totalCount: 0,
          totalPage: 0
        },
        multipleSelection:[],//
        AddForm :{
          birthday:"", // 
          email: "", // 
          gender: "", // 
          identityCard: "", // 
          roleIds: "", // 
          loginUser: "", // 
          mobilePhone: "", // 
          status: "1", // 
          userName: "", // 
          description: "", // 
          qqWeixin: "", // qq/
          password: "", // 
          id: ""
        },
        editVisible:false,
        rules: {
          loginUser: [
            {
              required: true,
              message: "  ",
              trigger: "blur"
            },
          ],
          userName: [
            {
              required: true,
              message: "  ",
              trigger: "blur"
            }
          ]

        },
        remnant_fou:200,
        genderData:[],
        titleData: [],
        statusData:[],
        //--end

    }
  },
  created(){
    this.getdata();
    this.getxiala();//
  },
  methods:{
    //
    handleAdd(){
      this.editVisible=true;
      //
      this.$nextTick(()=>{
        this.$refs["AddForm"].clearValidate();
      });

    },
    //--
    handleDeleteBatch(){
      let deletebatch = [];
      this.multipleSelection.forEach(i => {
        deletebatch.push(i.id);
      });
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http .post(this.api.userBatchDelete, deletebatch).then(res => {
              if (res.data.code == "200") {
                this.$message({
                  message: "",
                  type: "success"
                });
              }
              this.getdata();
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });

    },
    //
    search(){
      this.getdata();
    },
    //
    reset(){
      this.queryContent="";
      this.getdata();
    },
    //
    handleSelectionChange(val){
      if (val.length > 0) {
        this.multipleSelection = val;
        this.dicDisabled = false;
      } else {
        this.dicDisabled = true;
      }
    },
    //
    handleEdit(row){
      this.editVisible=true;
      this.AddForm = Object.assign({}, row);
      if(!!this.AddForm.roleIds){
        //String
        this.AddForm.roleIds = this.AddForm.roleIds.split(',')
      }
      this.$nextTick(()=>{
        this.$refs["AddForm"].clearValidate();
        });
      },

    //
    handleDelete(id){
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http.delete(this.api.userDeletebyid + id).then(res => {
            if (res.data.code == "200") {
              this.$message({
                message: "",
                type: "success"
              });
            }
            this.getdata();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    handlePage(number, size){
      this.pageData.pageNumber=number;
      this.pageData.pageSize=size;
      this.getdata();
    },
    //
    getdata(){
      this.$http
        .post(this.api.userQueryByCondition, {
          condition: {
            queryContent: this.queryContent,
          },
          number: this.pageData.pageNumber,
          size: this.pageData.pageSize
        })
        .then(res => {
          if (res.data.code == "200") {
            this.pageData.list = res.data.body.records;
            this.pageData.list.forEach(item => {
              //String
              item.roleIds = item.roleIds.split(',');
            })


            this.pageData.totalCount = res.data.body.total;
            this.pageData.totalPage = res.data.body.pages;
          }else{
            if(res.data.code == "401"){
                this.$message.warning(res.data.message);
            }
          }
        });
    },
    ///
    handleSave(){
      if(!!this.AddForm.roleIds){
        this.AddForm.roleIds=this.AddForm.roleIds.join(",")
      }else{
        this.AddForm.roleIds=''
      }
        this.$refs["AddForm"].validate(valid => {
              if (valid) {
                this.$http.post(this.api.userSaveOrUpdate, this.AddForm).then(res => {
                  if (res.data.code == "200") {
                      this.$message.success(res.data.body);
                      this.editVisible = false;
                      this.empty();
                      this.search();
                  }else{
                     this.$message.warning(res.data.body);
                  }
                });
              }else {
                return false;
              }
            })
    },
    //
    cancelSave(){
      this.editVisible=false;
      this.empty();
    },
    //
    descInput_fou(){
      var txtVal = this.AddForm.description.length;
      this.remnant_fou = 200 - txtVal;
    },
    //
    getxiala(){
      this.$http.get(this.api.dicTypeGetType + "enabled").then(res => {
        if (res.data.code == 200) {
          this.statusData = res.data.body;
        }
      });

      this.$http.post(this.api.roleGetList).then(res => {
        if (res.data.code == 200) {
          this.titleData = res.data.body;
        }
      });

      this.$http.get(this.api.dicTypeGetType + "gender").then(res => {
        if (res.data.code == 200) {
          this.genderData = res.data.body;
        }
      });

    },
    //
    empty(){
      this.AddForm ={
          birthday: "", // 
          email: "", // 
          gender: "", // 
          identityCard: "", // 
          roleIds: "", // 
          loginUser: "", // 
          mobilePhone: "", // 
          status: "1", // 
          userName: "", // 
          description: "", // 
          qqWeixin: "", // qq/
          password: "", // 
          id: ""
      }
    },
    //
    resetPassword(row){
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http
            .post(this.api.userResetPassword + row.id)
            .then(res => {
              if (res.data.code === "200") {
                this.$message.success(res.data.body);
              } else {
                this.$message.error(res.data.body);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

  },
  
}
</script>
<style lang="scss" scope>
</style>


