// 
import editComp from '@comp/common/editComp.vue';//
import treeSelect from '@comp/common/treeSelect/treeSelect.vue';//
import pagination from '@comp/common/pagination/index';//
import treeMultiSelect from '@comp/common/treeSelect/treeMultiSelect';//
import upload from '@comp/common/upload/upload';//
import uploadMore from '@comp/common/upload/uploadMore';//
import photoShow from '@comp/common/upload/photoShow';
import editor from '@comp/common/vueEditor/VueEditor';
import currencyInput from '@comp/common/input/currencyInput';
import uploadFile from '@comp/common/upload/uploadFile';//
import exportcom from '@comp/common/upload/exportcom';//
import NumberInput from '@comp/common/input/NumberInput';//
import queryItem from '@comp/common/UI/queryItem';//

//icon-svg

const components = [
  treeSelect,
  treeMultiSelect,
  upload,
  pagination,
  uploadMore,
  photoShow,
  editor,
  currencyInput,
  uploadFile,
  editComp,
  exportcom,
  NumberInput,
  queryItem
];

export default function(Vue, opts = {}) {
  components.map(component => {
    Vue.component(component.name, component);
  });
};


