<template>
    <div class="app-container-tree">
    
                    <div class="tree-area">
                        <div class="common-header">
                            <span class="common-header-title">
                                
                            </span>
                        </div>

                        <el-tree :data="treeData" :props="defaultProps" @node-click="handleNodeClick"> </el-tree>
                    </div>

                    <div class="table-area">
                        <div class="common-header">
                            <span class="common-header-title">
                                
                            </span>
                        </div>

                        <div class="treeTable-button" >

                            <span v-if="editModel">
                                <el-button type="primary" size="small"   @click="handleAdd"></el-button>
                                <el-button type="danger" size="small" :disabled="delable"  @click="handleDelete"></el-button>
                                <el-button type="primary"  size="small"   @click="handleEdit"></el-button>
                            </span>
                            <span v-else>
                                <el-button type="primary" size="small"   @click="handleSave"></el-button>
                                <el-button  plain size="small"   @click="handleCancel"></el-button>
                            </span>

                        </div>

                            <div style="margin-top: 42px;width:500px">
                                <el-form  ref="form" :model="form" :rules="rules" label-width="150px" size="small" style="margin-top:10px">


                                        <el-form-item label="" prop="testName" >
                                            <treeSelect v-model="form.parentId" :disabled="lookModel" :data="treeData" label="testName"/>
                                        </el-form-item>

                                        <el-form-item label="" prop="createTime">
                                                <el-date-picker
                                                        v-model="form.createTime"
                                                        type="datetime"
                                                        placeholder=""
                                                        default-time="12:00:00"
                                                        style="width:100%"
                                                        :disabled="lookModel"
                                                        >
                                                </el-date-picker>
                                        </el-form-item>

                                        <el-form-item label="" prop="testName" >
                                            <el-input v-model="form.testName" :disabled="lookModel" ></el-input>
                                        </el-form-item>


                                        <el-form-item label=""  prop="testStatus">
                                        <el-select v-model="form.testStatus"  placeholder=""  style="width:100%;" :disabled="lookModel">
                                            <el-option
                                                    v-for="item in statusList"
                                                    :key="item.value"
                                                    :label="item.label"
                                                    :value="item.value"
                                            ></el-option>
                                        </el-select>
                                        </el-form-item>
                
                                        <el-form-item label=""  >
                                            <el-select v-model="form.testType"  placeholder="" multiple style="width:100%;" :disabled="lookModel">
                                            <el-option
                                                v-for="item in typeList"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value"
                                                >
                                            </el-option>
                                            </el-select>
                                        </el-form-item>
                                </el-form>
                            </div>

                    </div>
        </div>
</template>
<script>
export default {
    name:"treeTable",
    data(){
        return{
            defaultProps:{
                children: 'children',
                label: 'testName'
            },
            treeData:[],
            form :{
                createTime: "",
                createUser: "",
                id: "",
                testName: "",
                testStatus: "",
                testType: "",
                parentId:"",
                testArea:""
            },
            rules:{},
                    //
        typeList: [
            {
              value: '1',
              label: ''
            }, {
              value: '2',
              label: ''
            }, {
              value: '3',
              label: ''
            }, {
              value: '4',
              label: ''
            }, {
              value: '5',
              label: ''
            }
          ],

          //
          statusList: [
            {
              value: '1',
              label: ''
            }, {
              value: '2',
              label: ''
            }
          ],

          remnant_fou: 200,
          delable:false,
          lookModel:true,
          editModel:true,
        }
    },
    created(){
        this.getData();
    },
    methods:{

        //
        handleNodeClick(data, node){
            Object.assign(this.form,data);
            var duoxuan=data.testType.split(',');
            this.form.testType=duoxuan;
            this.descInput_fou();
            this.delable=false;
        },

        //
        getData(){
        
            this.$http.get(this.api.sysTestGetTreeData)
            .then(res => {
            if (res.data.code == "200") {
                this.treeData = res.data.body;
            }
            });

        },

        //
        descInput_fou(){
            if(!this.form.testArea){
                this.remnant_fou=200;
            }else{
                var txtVal = this.form.testArea.length;
                this.remnant_fou = 200 - txtVal;
            }
         
        },

        //
        handleAdd(){
            this.statusOpen(false)
            this.empty();

        },

        //
        handleEdit(){
            this.statusOpen(false)
        },

        //
        handleDelete(){
            if(!this.form.id){
             
             return this.$message.error("");
            }

            if(!!this.form.children){
             
             return this.$message.error("");
            }

      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http.delete(this.api.sysTestDeletebyid + this.form.id).then(res => {
            if (res.data.code == "200") {
              this.$message({
                message: "",
                type: "success"
              });
            }
            this.empty();
            this.getData();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });



        },

        //
        handleSave(){
            var aa=this.form.createTime;
            var flag=typeof aa=='string'
            if(!!aa&&!flag){
                this.form.createTime=this.$dateUtils.dateUtils(this.form.createTime);
            }

            var flag2=typeof this.form.testType=='string';
            if(!!this.form.testType&&!flag2){
                this.form.testType=this.form.testType.join(",")
            }

            this.$http.post(this.api.sysTestSaveOrUpdate, this.form).then(res => {
              if (res.data.code == "200") {
                  this.$message.success(res.data.message);
                  this.empty();
                  this.statusOpen(true);
                  this.getData();
              }
            });
        },

        //
        handleCancel(){
            this.statusOpen(true)
        },

        statusOpen(boo){
            this.lookModel=boo;
            this.editModel=boo;
        },

        empty(){
            this.form ={
                createTime: "",
                createUser: "",
                id: "",
                testName: "",
                testStatus: "",
                testType: "",
                parentId:"",
                testArea:""
            }
        }


    }
    
}
</script>
<style scoped>

</style>


