<template>
    <div class="show-dialog">
        <el-dialog :visible.sync="editVisible" :show-close="false" :modal="false" fullscreen>
            <!---->
            <div class="dialog-button">
                <el-button  @click="backHandle"></el-button>
                <el-button type="primary" @click="saveHandle"></el-button>

            </div>
            <div class="dialog-form">
                <el-form ref="form" label-width="150px" :rules="rules" :model="form">

                    <el-form-item label="" prop="terminalModel">
                        <el-input v-model="form.terminalModel"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="terminalName">
                        <el-input v-model="form.terminalName"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="terminalNumber">
                        <el-input v-model="form.terminalNumber"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="terminalNameType">
                        <el-input v-model="form.terminalNameType"></el-input>
                    </el-form-item>
                </el-form>
            </div>
        </el-dialog>
    </div>
</template>
<script>
export default {
    name: "phoneModelEdit",
    data() {
      return {
        editVisible: false,
        //
        form: {},
        //
        selectData: {
        },
        //
        rules:{
        }

      }
    },
    methods:{
        //
        clearCheck(){
            this.$refs['form'].resetFields()
        },
        //
        empty(){
            this.form = {
                terminalModel:"",
                terminalName:"",
                terminalNumber:"",
                terminalNameType:"",
            }
        },
        //
        openByNew(){
            this.editVisible = true
            this.empty()
        },
        //
        openByEdit(row){
            this.openByNew()
            this.$http.get("api/system/phoneModel/queryById/" + row.id).then(res => {
                this.form = res.data.body
            });
        },
        //
        backHandle(){
            this.clearCheck()
            this.editVisible = false
        },
        //
        saveHandle(){
            this.$refs['form'].validate((valid)=>{
              if(valid){
                this.$confirm("?", "", {
                  confirmButtonText: "",
                  cancelButtonText: "",
                  type: "warning"
                }).then(() => {
                    this.$http.post("api/system/phoneModel/saveOrUpdate",this.form).then(res => {
                      if(res.data.code == '200'){
                        this.$message.success(res.data.message)
                        this.editVisible = false
                        this.$emit("back")
                      }
                    });
                })
              }
            })
        }

    }

}
</script>


