<template>
    <div class="app-container">
      <el-scrollbar style="width:100%">
        <!--  -->
        <div class="container-query">
            <queryItem label="">
                <el-input v-model="queryContion.testName" placeholder=""></el-input>
            </queryItem>
            <queryItem label="">
                <el-select v-model="queryContion.testStatus" placeholder=""  clearable>
                  <el-option
                    v-for="item in selectData.testStatusList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
            </queryItem>
            <queryItem  label="" style="width:50%">
                <div class="is_data">
                  <el-date-picker
                    v-model="queryContion.createTimeFrom"
                    type="date"
                    placeholder="">
                  </el-date-picker>
                  <span style="margin: 0 10px;line-height: 36px;">  </span>
                  <el-date-picker
                    v-model="queryContion.createTimeTo"
                    type="date"
                    placeholder="">
                  </el-date-picker>
                </div>
            </queryItem>
            <query-item isButton style="width:100%;padding-left:30px">
                    <el-button type="primary"  @click="search" ></el-button>
                    <el-button  plain @click="reset" ></el-button>
                    <el-button  plain @click="handleAdd" ></el-button>
                    <el-button type="danger" @click="handleDeleteBatch" ></el-button>
            </query-item>
           
        </div>
        <!--  -->
        <div class="container-table">
            <div class="common-table-style">
                <el-table :data="pageData.list" border @selection-change="handleSelectionChange">
                    <el-table-column type="selection"></el-table-column>
                    <el-table-column type="index" width="50" label=""></el-table-column>
                    <el-table-column prop="createTime"  label="" ></el-table-column>
                    <el-table-column prop="testName"  label="" ></el-table-column>
                    <el-table-column prop="testStatus"  label="" align="center" >
                      <template slot-scope="scope">
                          <el-tag v-for="item in selectData.testStatusList" :key="item.id" v-show="item.value == scope.row.testStatus">{{item.label}}</el-tag>
                      </template>
                    </el-table-column>
                    <el-table-column prop="testNum" label="" >
                      <template slot-scope="scope">
                        <span>{{scope.row.testNum | 10}}</span>
                      </template>
                    </el-table-column>
                    <el-table-column prop="testUnit"  label="" >
                      <template slot-scope="scope">
                        <span>{{scope.row.testUnit | 1000}}</span>
                      </template>
                    </el-table-column>
                    <el-table-column prop="testTotal"  label="">
                      <template slot-scope="scope">
                        <span>{{scope.row.testTotal | 1000}}</span>
                      </template>
                    </el-table-column>
                    <el-table-column  label="">
                      <template slot-scope="scope">
                          <el-button @click="handleEdit(scope.row)" type="text"></el-button>
                          <el-button @click="handleDelete(scope.row)" type="text" style="color:red"></el-button>
                      </template>
                    </el-table-column>
                  </el-table>
            </div>
            <div class="pagination">
              <pagination :page-list="pageData" @pagesearch="handlePage"></pagination>
            </div>
        </div>
        <oneEdit ref="oneEdit"></oneEdit>
      </el-scrollbar>
        
    </div>
</template>
<script>
import oneEdit from "./edit"
export default {
    components: { oneEdit },
    name: "oneQuery",
    data() {
      return {
        // 
        pageData: {
          list: [],
          pageNumber: 1,
          pageSize: 10,
          totalCount: 0,
          totalPage: 0
        },
        // 
        queryContion: {
          testName: "",
          createTimeFrom:"",
          createTimeTo:"",
          testStatus:""
        },
        //
        selectData: {
          testStatusList:[],
          
        },

        //
        delVal:""

      }
    },
    created() {
      //
      this.getDropData()
      //
      this.getData()

    },
    methods:{
        //
        getDropData(){
            this.$http.get(this.api.dicTypeGetType + "enabled").then(res => {
                this.selectData.testStatusList = res.data.body
            })
        },
        // 
        empty(){
            this.queryContion = {
              name: "",
              createTime: ""
            }
        },
        //
        getData(){
            let queryContion = {
                condition: this.queryContion,
                number: this.pageData.pageNumber,
                size: this.pageData.pageSize
            }
            this.$http.post(this.api.sysTestquery,queryContion).then(res => {
              this.pageData.list = res.data.body.records;
              this.pageData.totalCount = res.data.body.total;
              this.pageData.totalPage = res.data.body.pages;
            });
        },

        // 
        search(){
            this.getData()
        },
        // 
        reset(){
            this.empty()
            this.getData()
        },

        //
        handlePage(number, size){
            this.pageData.pageNumber = number;
            this.pageData.pageSize = size;
            this.getData();
        },

        //
        handleAdd() {
          this.$refs.oneEdit.openByNew()
        },

        //
        handleEdit(row) {
          this.$refs.oneEdit.openByEdit(row)
        },
        //
        handleDelete(row){
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }).then(() => {
              this.$http.delete(this.api.sysTestdeleteById + row.id).then(res => {
                if(res.data.code == '200'){
                  this.$message.success(res.data.message)
                  this.getData()
                }
              });
          })
        },
        handleDeleteBatch(){
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }).then(() => {
            let deletebatch = this.$utils.findIds(this.delVal)
            this.$http.post(this.api.sysTestbatchDelete,deletebatch).then(res => {
              if(res.data.code == '200'){
                this.$message.success(res.data.message)
                this.getData()
              }
            });
          })
        },
        //
        handleSelectionChange(val){
          this.delVal = val;
        }
    }
}
</script>


