<template>
<div class="app-container">
  <div class="show-dialog">
    <el-dialog :visible.sync="editVisible" :show-close="false" :modal="false" fullscreen>
         <div class="dialog-button" >
            <el-button  icon="el-icon-back"></el-button>
            <el-button type="primary"  icon="el-icon-check"></el-button>
            <el-button type="primary"  icon="el-icon-check"></el-button>
        </div>

        <editComp width="50%" title="">
            <el-form label-width="90px">
              <el-form-item label="" style="width:50%;">
                <el-select v-model="orgValue" placeholder="">
                  <el-option
                    v-for="item in orgList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" >
                
              </el-form-item>
              <el-form-item label="">
                
              </el-form-item>
              <el-form-item label="">
                
              </el-form-item>

              <el-form-item label="*">
                <el-select v-model="orgValue" placeholder=""  clearable>
                  <el-option
                    v-for="item in orgList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                
              </el-form-item>
              <el-form-item label="">
                
              </el-form-item>
              <el-form-item label="">
                
              </el-form-item>
              <el-form-item label="">
                
              </el-form-item>
              <el-form-item label="1">
                
              </el-form-item>
              <el-form-item label="">
                
              </el-form-item>
              <el-form-item label="" style="width:50%;">
                
              </el-form-item>
             <el-form-item label="2">
                
              </el-form-item>
              <el-form-item label="">
                
              </el-form-item>
              <el-form-item label="" style="width:100%;">
                <div style="line-height: 22px;margin-top:5px">
                  <el-link type="primary" ></el-link>
                  <el-link type="primary"></el-link>
                  <el-link type="danger"></el-link>
                </div>
              </el-form-item>
              <el-form-item label="" style="width:100%;">
                <div style="line-height: 22px;margin-top:5px">
                  <el-link type="primary" ></el-link>
                  <el-link type="primary"></el-link>
                  <el-link type="danger"></el-link>
                </div>

              </el-form-item>

            </el-form>
        </editComp>

        <editComp width="50%" title="" style="height:320px;">
            <el-table :data="tableData" show-summary border  align="center">
              <el-table-column prop="name" ></el-table-column>
              <el-table-column prop="totalPrice" label=""></el-table-column>
              <el-table-column prop="thisYearPrice" label=""></el-table-column>
              <el-table-column prop="paidPrice" label=""></el-table-column>
              <el-table-column prop="nextYearPrice" label=""></el-table-column>
              <el-table-column prop="needPaid" label=""></el-table-column>
            </el-table>
        </editComp>

        <editComp width="100%" title="" >

            <div class="container-btn">
                <el-button icon="el-icon-plus"  type="primary" @click="addList"></el-button>
                <el-button icon="el-icon-minus"  type="danger" @click="delList"></el-button>
            </div>
              <div class="tab-table">
                <el-table @selection-change="handleSelectionChangeDetil" :data="budgetDetailsList" border align="center">

                  <el-table-column type="selection"  width="45" align="center"></el-table-column>
                  <el-table-column type="index" width="55"  align="center"></el-table-column>
                  <el-table-column prop="budgetSubject"  label="" align="center" >
                    <template slot="header" slot-scope="scope">
                      <span style="color:red">*</span>
                      <span>{{scope.column.label}}</span>
                    </template>
                    <template slot-scope="scope">
                        <el-select v-model="scope.row.budgetSubject"  placeholder="" >
                          <el-option
                            v-for="item in subjectList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                          ></el-option>
                        </el-select>
                    </template>
                  </el-table-column>

                  <el-table-column prop="budgetNum"  label="" >
                    <template slot="header" slot-scope="scope">
                      <span style="color:red">*</span>
                      <span>{{scope.column.label}}</span>
                    </template>
                    <template slot-scope="scope">
                        <el-input  v-model="scope.row.budgetNum"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column prop="budgetTotal" label="" align="center">

                    <template slot-scope="scope">
                      {{scope.row.budgetTotal}}
                    </template>
                  </el-table-column>

                  <el-table-column prop="thisYearBudgetPrice" label="" align="center" >
                    <template slot="header" slot-scope="scope">
                      <span style="color:red">*</span>
                      <span>{{scope.column.label}}</span>
                    </template>
                    <template slot-scope="scope">
                      <el-input  v-model="scope.row.thisYearBudgetPrice"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column  prop="paidPrice" label="" align="center" >
                    <template slot="header" slot-scope="scope">
                      <span style="color:red">*</span>
                      <span>{{scope.column.label}}</span>
                    </template>
                    <template slot-scope="scope">
                      <el-input  v-model="scope.row.paidPrice"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column  prop="nextpayPrice" label="" align="center" >
                    <template slot="header" slot-scope="scope">
                      <span style="color:red">*</span>
                      <span>{{scope.column.label}}</span>
                    </template>
                    <template slot-scope="scope">
                      <el-input  v-model="scope.row.nextpayPrice"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column  prop="otherYearpayPrice" label="" align="center" >
                    <template slot="header" slot-scope="scope">
                      <span style="color:red">*</span>
                      <span>{{scope.column.label}}</span>
                    </template>
                    <template slot-scope="scope">
                      <el-input  v-model="scope.row.otherYearpayPrice"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column  prop="remark" label="" align="center" >
                    <template slot-scope="scope">
                      <el-input  v-model="scope.row.remark"></el-input>
                    </template>
                  </el-table-column>

                </el-table>
              </div>

        </editComp>
    </el-dialog>
  </div>
</div>
</template>
<script>
  export default {
    name: "editDemo",
    data() {
      return {
        editVisible: true,
        orgValue:'1',
        orgList:[
          {
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }
        ],
        tableData:[
          {
            name:"",
            totalPrice:1000000,
            thisYearPrice:1000000,
            nextYearPrice:0,
            paidPrice:0,
            needPaid:0
          },
          {
            name:"",
            totalPrice:1000000,
            thisYearPrice:1000000,
            nextYearPrice:0,
            paidPrice:0,
            needPaid:0
          },
          {
            name:"",
            totalPrice:1000000,
            thisYearPrice:1000000,
            nextYearPrice:0,
            paidPrice:0,
            needPaid:0
          }
        ],
        subjectList: [
          {
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }
        ],
        budgetDetailsList:[],
        delVal: "",
        
      }
    },
    created() {

    },
    methods:{

      handleSelectionChangeDetil(val){
        this.delVal = val;
      },
      addList(){
        var budgetDetailsListItem = {
            budgetSubject:"",
            budgetNum:null,
            budgetTotal:null,
            thisYearBudgetPrice:null,
            paidPrice:null,
            nextpayPrice:null,
            otherYearpayPrice:null,
            remark:""
        }
        if (!this.budgetDetailsList) {
          this.budgetDetailsList = [];
        }
        this.budgetDetailsList.push(budgetDetailsListItem);
      },
      delList(){
        if (this.delVal.length == 0) {
          return this.$message.warning("");
        }
        this.budgetDetailsList = this.minus(this.budgetDetailsList, this.delVal);
      },
      //
      minus(arr1, arr2) { 
        let arr3 = [];
        for (var i = 0; i < arr1.length; i++) {
          var flag = true;
          for (var j = 0; j < arr2.length; j++) {
            if (arr1[i] == arr2[j]) flag = false;
          }
          if (flag) {
            arr3.push(arr1[i]);
          }
        }
        return arr3;
      }

    }
  }
</script>
<style scoped>

</style>


