<template>
<div class="edit-frame">
    <div class="edit-frame-left">
        <!-- start-->
        <template v-if="needParam.layoutType == '1'">
            <template v-if="needParam.isShowQuery">
            <!-- -->
            <div class="container-query" >
                <template v-for="(item,index) in needParam.queryList" >
                    <!-- -->
                    <queryItem v-if="item.type == '1'" :label="item.title+''" :key="index">
                        <el-input  clearable></el-input>
                    </queryItem>
                    <!-- -->
                    <queryItem v-if="item.type == '2'" :label="item.title+''" :key="index">
                        <el-select v-model="currencyValue" placeholder=""  clearable>
                        <el-option
                            v-for="item in currencyList"
                            :key="item.id"
                            :label="item.fieldName"
                            :value="item.id"
                        ></el-option>
                        </el-select>
                    </queryItem>
                    <!-- -->
                    <queryItem v-if="item.type == '3'" :label="item.title+''" :key="index">
                        <el-date-picker
                            type="daterange"
                            align="right"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            value-format="yyyy-MM-dd HH:mm:ss"
                            ></el-date-picker>
                    </queryItem>

                </template>
                <query-item isButton style="width:100%">
                     <!-- -->
                    <template v-for="(item,index) in needParam.queryList">
                       <el-button style="margin-left:10px" v-if="item.type == '4'&&item.value == '4'" :key="index" @click="handleAdd">{{item.title}}</el-button>
                       <el-button style="margin-left:10px" v-if="item.type == '4'&&item.value != '4'" :key="index">{{item.title}}</el-button>
                    </template>
                </query-item>
            </div>
            </template>
            <!-- -->
            <template v-show="needParam.isShowTable">
                <div class="container-table">
                    <div class="common-table-style">
                        
                        <el-table :data="pageData.list" border style="width: 100%">
                            <el-table-column type="selection" width="55" v-if="needParam.isShowCheckTable" ></el-table-column>
                            <el-table-column type="index" width="50" label="#"></el-table-column>
                            <el-table-column
                            v-for="(item,index) in needParam.tableList"
                            :key="index"
                            :prop="item.value"
                            :label="item.title">
                            aaaaa
                            </el-table-column>
                            <el-table-column v-if="needParam.isShowOperaTable"  width="200px" label="">
                                <slot slot-scope="scope">
                                    <el-button v-for="(item,index) in needParam.tableButtonList"  :key="index">{{item.title}}</el-button>
                                </slot>
                            </el-table-column>
                        </el-table>
                    </div>
                    <div class="pagination" v-if="needParam.isShowPage">
                        <pagination :page-list="pageData"></pagination>
                    </div>
                </div>
            </template>
        </template>
        <editTem ref="editTem" :editList="needParam.editList"></editTem>
         <!-- end-->
    </div>

    <div class="edit-frame-right" :style="{height:frameRight+'px'}">
    <el-scrollbar style="height:100%">
        <div class="line-div">
            <el-button class="line-div-button" @click="back"></el-button>
            <el-button class="line-div-button" @click="saveParm" v-show="!isFromTem"></el-button>
            <el-button class="line-div-button" @click="openDigCode" v-show="!isFromTem"></el-button>
            <el-button class="line-div-button" @click="openDigTem" v-show="isFromTem"></el-button>
            <div v-show="!isFromTem">
                <span></span>
                <el-select v-model="currencyValue" placeholder="" @change="changeTemp" clearable>
                <el-option
                    v-for="item in templateList"
                    :key="item.id"
                    :label="item.temPlateName"
                    :value="item.id"
                ></el-option>
                </el-select>
            </div>
        </div>

        <div class="line-div">
            <el-radio v-model="needParam.layoutType" label="1"></el-radio>
            <el-radio v-model="needParam.layoutType" label="2"></el-radio>
        </div>

        <div class="line-div">
            <el-checkbox v-model="needParam.isShowQuery"></el-checkbox>
            <el-checkbox v-model="needParam.isShowTable"></el-checkbox>
            <el-checkbox v-model="needParam.isShowPage"></el-checkbox>
            <el-checkbox v-model="needParam.isShowEdit"></el-checkbox>
        </div>


        <el-collapse class="data-area" v-model="activeName" accordion>
        <el-collapse-item v-show="needParam.isShowQuery" title="" name="1">
            <div class="add-query">
                <div class="add-query-input">
                    <el-select v-model="addType" placeholder=""  clearable>
                    <el-option
                        v-for="item in addTypeList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                    </el-select>
                </div>
                <div class="add-query-button">
                    <el-button @click="addQueryItem"></el-button>
                </div>
            </div>
            <div class="data-area-item" v-for="(item,index) in needParam.queryList" :key="index">
                <i type="text" class="data-area-button el-icon-delete" @click="delItem(index)"></i>
                <template v-if="item.type != '4'">
                    <el-input class="data-area-input" placeholder="" v-model="item.title"></el-input>
                    <el-select class="data-area-input" v-model="item.value" placeholder=""  clearable>
                    <el-option
                        v-for="item in tableFileds"
                        :key="item.id"
                        :label="item.fieldName"
                        :value="item.id"
                    ></el-option>
                    </el-select>
                </template>
                <template v-if="item.type == '4'">
                    <el-input class="data-area-input" placeholder="" v-model="item.title"></el-input>
                    <el-select class="data-area-input"  v-model="item.value" placeholder=""  clearable>
                    <el-option
                        v-for="item in buttonDataSource"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                    </el-select>
                </template>
            </div>
        </el-collapse-item>
        <el-collapse-item v-show="needParam.isShowTable" title="" name="2">
            <div class="add-query">
                <div class="add-query-button">
                    <el-button @click="addTableItem"></el-button>
                    <el-checkbox style="margin-left:10px" v-model="needParam.isShowCheckTable"></el-checkbox>
                    <el-checkbox v-model="needParam.isShowOperaTable"></el-checkbox>
                </div>
            </div>
            <div class="data-area-item" v-for="(item,index) in needParam.tableList" :key="index">
                <i type="text" class="data-area-button el-icon-delete" @click="delTableItem(index)"></i>
                <el-input class="data-area-input" placeholder="" v-model="item.title"></el-input>
                <el-select class="data-area-input" v-model="item.value" placeholder=""  clearable>
                <el-option
                    v-for="item2 in tableFileds"
                    :key="item2.id"
                    :label="item2.fieldName"
                    :value="item2.id"
                ></el-option>
                </el-select>
            </div>
            <div v-show="needParam.isShowOperaTable">
            <el-button @click="addOperaTable"></el-button>
            <div  class="data-area-item" v-for="(item,index) in needParam.tableButtonList" :key="'operaTablekey-'+item.paramIndex">
                <i type="text" class="data-area-button el-icon-delete" @click="delOperaTableItem(index)"></i>
                <el-input class="data-area-input" placeholder="" v-model="item.title"></el-input>
                <el-select class="data-area-input" v-model="item.value" placeholder=""  clearable>
                <el-option
                    v-for="item2 in tableButtonCheckList"
                    :key="item2.value"
                    :label="item2.label"
                    :value="item2.value"
                ></el-option>
                </el-select>
            </div>
            </div>
        </el-collapse-item>

        <el-collapse-item v-show="needParam.isShowEdit" title="" name="3">
            <div class="add-query">
                <div class="add-query-input">
                    <el-select v-model="addEditType" placeholder=""  clearable>
                    <el-option
                        v-for="item in addTypeList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                    </el-select>
                </div>
                <div class="add-query-button">
                    <el-button @click="addEditItem"></el-button>
                </div>
            </div>
            <div class="data-area-item" v-for="(item,index) in needParam.editList" :key="index">
                <i type="text" class="data-area-button el-icon-delete" @click="delEditItem(index)"></i>
                <template v-if="item.type != '4'">
                    <el-input class="data-area-input" placeholder="" v-model="item.title"></el-input>
                    <el-select class="data-area-input" v-model="item.value" placeholder=""  clearable>
                    <el-option
                        v-for="item in tableFileds"
                        :key="item.id"
                        :label="item.fieldName"
                        :value="item.id"
                    ></el-option>
                    </el-select>
                </template>
                <template v-if="item.type == '4'">
                    <el-input class="data-area-input" placeholder="" v-model="item.title"></el-input>
                    <el-select class="data-area-input"  v-model="item.value" placeholder=""  clearable>
                    <el-option
                        v-for="item in editButtonDataSource"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                    </el-select>
                </template>
            </div>
        </el-collapse-item>
        </el-collapse>
    </el-scrollbar>
    </div>

    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
        <div>
            <el-form :inline="true" :model="projModel">
                <el-form-item label="">
                    <el-input v-model="projModel.packageName"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="projModel.projectName"></el-input>
                </el-form-item>
            </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="greatCode"> </el-button>
        </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="temVisible" width="30%">
    <div>
        <el-form :inline="true" :model="projModel">
            <el-form-item label="">
                <el-input v-model="projModel.temPlateName"></el-input>
            </el-form-item>
        </el-form>
    </div>
    <span slot="footer" class="dialog-footer">
        <el-button @click="temVisible = false"> </el-button>
        <el-button type="primary" @click="generateTemplate"> </el-button>
    </span>
    </el-dialog>
</div>

</template>

<script>
import editTem from "./editTem"
export default {
    name:"editLayout",
    components: { editTem },
    data(){
        return{
            currencyList:[],//list
            currencyValue:'',//
            projModel:{
                tableId:"",
                packageName:"",
                projectName:"",
                temPlateName:""
            },
            needParam:{
                tableId:"",
                layoutType:'1',//1.2.
                isShowQuery:true,//
                isShowTable:true,//
                isShowPage:true,//
                isShowEdit:true,//
                isShowOperaTable:true,//table
                isShowCheckTable:true,
                queryList:[],//
                tableList: [],//
                tableButtonList:[],//
                editList:[],
            },
            activeName: '1',
            tableFileds:[],
            addType:'',//
            addTypeList:[
                {value:'1',label:''},
                {value:'2',label:''},
                {value:'3',label:''},
                {value:'4',label:''}
            ],
            dropDataSource:[
                {value:'1',label:''}
            ],
            editButtonDataSource:[
                {value:'1',label:'',fun:''},
                {value:'5',label:'',fun:''}
            ],
            buttonDataSource:[
                {value:'1',label:'',fun:''},
                {value:'2',label:'',fun:''},
                {value:'3',label:'',fun:''},
                {value:'4',label:'',fun:'handleAdd'},
                {value:'5',label:'',fun:''}
            ],
            pageData: {
                list: [{}],
                pageNumber: 1,
                pageSize: 10,
                totalCount: 1,
                totalPage: 1
            },
            editTableId:"",
            addEditType:"",
            dialogVisible:false,
            tableButtonCheckList:[
                {value:'1',label:''},
                {value:'2',label:''},
                {value:'3',label:''}
            ],
            temVisible:false,
            templateList:[],//
        }
    },
    props:{
        isFromTem:{
            type:Boolean,
            default:false
        }
    },
    computed:{
        frameRight(){
            return document.getElementsByClassName('right-body')[0].clientHeight - 40;
        }
    },
    created(){
        this.getSelect()
    },
    methods:{
        /**  start*/
        //
        openByNew(){
            this.empty()
        },
        //
        openDigTem(){
            this.temVisible = true
        },
        //
        generateTemplate(){
            if(!this.projModel.temPlateName){
                return this.$message.warning("")
            }
            let param = {
                id:this.editTableId,
                isTemplate:"1",
                temPlateName:this.projModel.temPlateName,
                autoParam:this.needParam
            }
            this.$http.post(this.api.sysAutoTableSaveOrUpdateTem,param).then(res => {
                if (res.data.code == "200") {
                    this.$message.success("");
                    this.back()
                }
            })
        },
        updateInput(){
            this.$forceUpdate()
        },
        /**  end*/
        //
        changeTemp(val){
            this.getAutoParamData(val)
        },
        //
        getSelect(){
            this.$http.post(this.api.sysAutoTableQuerybycondition,{
                condition: {isTemplate : '1'},
                number: 1,
                size: 999
            }).then(res => {
                if (res.data.code == "200") {
                    this.templateList = res.data.body.records
                }
            })
        },
        //
        handleAdd() {
            if(this.needParam.isShowEdit){
                this.$refs.editTem.openByNew()
            }
        },
        delTableItem(index){
            this.needParam.tableList.splice(index,1)
        },
        delItem(index){
            this.needParam.queryList.splice(index,1)
        },
        delOperaTableItem(index){
            this.needParam.tableButtonList.splice(index,1)
            this.$forceUpdate()
        },
        delEditItem(index){
            this.needParam.editList.splice(index,1)
        },
        //
        addEditItem(){
            if(!this.addEditType){
                return this.$message.warning("");
            }
            if(!this.needParam.editList){
                this.needParam.editList = []
            }
            var item = {
                type:this.addEditType,
                title:"",
                value:"",
                paramIndex:this.needParam.editList.length
            }
            this.needParam.editList.push(item)
            this.$forceUpdate()
        },
        //
        addOperaTable(){
            if(!this.needParam.tableButtonList){
                this.needParam.tableButtonList = []
            }
            var item = {
                title:"",
                value:"",
                paramIndex: this.needParam.tableButtonList.length
            }
            this.needParam.tableButtonList.push(item)
            this.$forceUpdate()
        },
        addTableItem(){
            if(!this.needParam.tableList){
                this.needParam.tableList = []
            }
            var item = {
                title:"",
                value:"",
                paramIndex: this.needParam.tableList.length
            }
            this.needParam.tableList.push(item)
        },
        addQueryItem(){
            if(!this.addType){
                return this.$message.warning("");
            }
            if(!this.needParam.queryList){
                this.needParam.queryList = []
            }
            var item = {
                type:this.addType,
                title:"",
                value:"",
                paramIndex:this.needParam.queryList.length
            }
            this.needParam.queryList.push(item)
        },
        //
        openDigCode(){
            this.projModel = {
                tableId:"",
                packageName:"com.dhlg",
                projectName:"system",
            }

            if(!this.editTableId){
                return this.$message.error("");
            }
            this.dialogVisible = true
        },
        greatCode(){
            //
            this.$confirm("", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
            }).then(() => {
                this.projModel.tableId = this.editTableId
                this.$http.post(this.api.sysAutoTableCodeGeneration,this.projModel).then(res => {
                    if (res.data.code == "200") {
                        this.$message.success(res.data.message);
                        this.back()
                    }
                })
            }).catch(() => {
                this.$message.info("");
            });
        },
        //
        saveParm(){
            this.needParam.tableId = this.editTableId;
            if(!this.needParam.tableId){
                return this.$message.warning("")
            }
            this.$confirm("", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
            }).then(() => {
                this.savePost()
            }).catch(() => {
                this.$message.info("");
            });
        },
        savePost(){
            this.$http.post(this.api.sysAutoParamSaveOrUpdate,this.needParam).then(res => {
                if (res.data.code == "200") {
                    this.$message.success("");
                }
            })
        },
        editInit(row){
            //
            this.getdropData(row.id)
            this.editTableId = row.id
            //id
            this.getAutoParamData(row.id)
        },
        getAutoParamData(id){
            this.$http.get(this.api.sysAutoParamFindByTableID + id).then(res => {
                if (res.data.code == "200") {
                    let data = res.data.body
                    if(!data){
                        this.empty()
                    }else{
                        this.needParam = data
                    }
                    
                }
            })
        },
        getdropData(id){
            this.$http.get(this.api.sysAutoFieldFindByTableID + id).then(res => {
                if (res.data.code == "200") {
                    this.tableFileds = res.data.body
                }
            })
        },
        back(){
            this.$emit("backfont")
        },
        empty(){
            this.needParam = {
                tableId:"",
                layoutType:'1',//1.2.
                isShowQuery:true,//
                isShowTable:true,//
                isShowPage:true,//
                isShowEdit:true,//
                isShowOperaTable:true,//table
                isShowCheckTable:true,
                queryList:[],//
                tableList: [],//
                tableButtonList:[],//
                editList:[],
                temPlateName:""
            }
        },

    }
}
</script>
<style  scoped>
.line-div-button{
    margin-bottom: 5px;
}
.edit-frame{
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
}
.edit-frame-left{
    display: flex;
    flex: 1;
    flex-direction: column;
    margin-right: 10px;
    position: relative;
}
.edit-frame-right{
    width: 300px;
    background: #fff;
    padding: 10px 10px;
    display: flex;
    flex-direction: column;
}
.container-query2{
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 1px 1px 3px rgba(0,0.2,0,0.2);
    background: #fff;
    padding: 5px 30px;
}
.container-table2{
    height: 100%;
    background-color: #ffffff;
    box-shadow: 1px 1px 3px rgba(0,0,0,0.2);
    padding: 10px 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
}
.line-div{
    margin-bottom: 10px;
}
.data-area{
    margin-top: 20px;
}
.data-area-checkbox{
    width: 120px;
    height: 33px;
    overflow:hidden; /**/ 
}
.data-area-input{
    display: flex;
    flex: 1;
}
.data-area-item{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    line-height: 32px;
}
.data-area-item >.data-area-input{
    margin: 5px 5px;
    width: 100px;
}
.add-query{
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
}
.add-query-input{
    display: flex;
    flex: 1;
}
.add-query-button{
    display: flex;
    justify-content: flex-end;
    margin-right: 20px;
}
.data-area-button{
    width: 32px;
    height: 32px;
    font-size: 18px;
    color: red;
    text-align: center;
    margin-top: 10px;
    cursor:pointer
}
</style>


