import router from '@router/index'
import store from '@/store/index'

import Layout from '@comp/layout/text'
import page404 from '@comp/layout/page/404'
import Dashboard from '@views/modules/dashboard/Dashboard'
import oneTemplate from '@views/template/one/query'
// import oneTemplate from '@views/modules/phoneModel/query'
import User from "@utils/common/UserContext"

var getRouter=null //

router.beforeEach((to, from, next) => {
    //
    if(to.path=='/login'||to.path=='/page/404'){
        return next();
    }
    //token,token
    if(!getToken()){
      return next('/login')
    }
    //
    var loginStatus = getLoginStatus();
    //
    if (loginStatus) {
        getRouter = getObjArr()
        routerGo(to, next)
    } else {
      try{
        next()
      }catch(e){
        next('/page/404')
      }
    }

})


function routerGo(to, next) {
  var loginStatus = getLoginStatus();
  if(loginStatus){
    //
    getRouter = filterAsyncRouter(getRouter)
    //
    router.addRoutes(getRouter) 
    //
    changeLoginStatus()
  }
  
  next(to.path)
}

//
function getObjArr() {
  return User.getMenuData()
}
//
function changeLoginStatus() {
  store.state.addRouter.loginStatus=false
}
// 
function getLoginStatus() {
  return store.state.addRouter.loginStatus
}
//
function filterAsyncRouter(routers) {
  
  //LayoutDashboardchildren
  let fakeRouter=[
    {
      path: "/Layoutpage",
      component: Layout,
      children:[
        {
          path: "/page/Dashboard",
          name: "",
          component: Dashboard,
        },
        {
          path: "/page/404",
          name: "404",
          component: page404,
        },
        {
          path: "/one",
          name: "one",
          component: oneTemplate,
        },
      ]
    },
    {
      path: "*",
      redirect: "/login"
    },
  ]

  //
  if(routers){
    let fiterRouter=treeIteration(routers)
      //list
    let baseRouter=fakeRouter[0].children
    if(!!fiterRouter){
      baseRouter=baseRouter.concat(fiterRouter)
    }
    fakeRouter[0].children=baseRouter;
  }
  return fakeRouter
}

//
function treeIteration(list) {
  var kidRouters=[]
  list.forEach(v => {
    //componentcomponent404
    if(!v.children){
      var comp;
      var routerItem;
      try{
        comp=loadView(v.url);
      }catch(e){
        comp=page404;
      }
      var routerItem={
        path: v.url,
        name: v.menuName,
        component: comp
      }
      kidRouters.push(routerItem);
    }
    //list
    if(!!v.children){
      let treeList=treeIteration(v.children)
      kidRouters=kidRouters.concat(treeList)
    }
    
  });

  return kidRouters;
}

function loadView(view){
  // ,catch
  require('@/views' + view + '.vue');

  return ()=> import('@/views' + view + '.vue');

}
function getToken(){
  return User.getToken()
}



